.headerOptions {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  margin-right: 20px;
  color: grey;
}

.headerOptions:hover {
  color: black;
}

.headerOption__title {
  font-weight: 400;
  font-size: 12px;
}

.headerOption__icon {
  object-fit: contain;
  height: 25px !important;
  width: 25px !important;
}
